import useLocales from '../../hooks/useLocales';

import UserList from './UserList';

import { BlurModalBox } from '../../components/StyledComponents';

export default function UserListModal({ channels, isReceiver, openChat, onClose }) {
    const { t } = useLocales();
    return (
        <>
            <input type="checkbox" id="user-list-modal" className="modal-toggle" />
            <div className="modal">
                <BlurModalBox className="modal-box backdrop-blur-lg">
                    <div className="mb-2">
                        <label
                            htmlFor="user-list-modal"
                            className="btn btn-sm btn-circle absolute right-2 top-5 bg-transparent border-0"
                        >
                            âœ•
                        </label>
                        <h3 className="font-bold text-lg">{t('title.modal.select_user')}</h3>
                    </div>
                    <hr className="-mx-5 border-stone-500 mb-4" />

                    <div className="flex flex-col gap-4 p-2 mb-4">
                        <UserList channels={channels} openChat={openChat} isReceiver={isReceiver} />
                    </div>
                    <div className="flex justify-end gap-2 p-2">
                        <button className="btn btn-primary px-8 capitalize btn-outline" onClick={onClose}>
                            {t('action.confirm')}
                        </button>
                    </div>
                </BlurModalBox>
            </div>
        </>
    );
}
