import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Icon } from '@iconify/react';

import { API_CHAT, API_TICKET } from '../../config';
import useLocales from '../../hooks/useLocales';

import axios from '../../utils/axios';
import { fDescriptionTypeDate } from '../../utils/formatTime';
import { toast } from 'react-toastify';

import { ChatPanel, UserList } from '../../sections/conversation';
import UserListModal from '../../sections/conversation/UserListModal';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';
import useAuth from '../../hooks/useAuth';

export default function ConversationYard() {
    const { t } = useLocales();
    const { user } = useAuth();
    const { ticketId, isReceived } = useParams();

    const [channels, setChannels] = useState([]);
    const [channel, setChannel] = useState();
    const [selectedChannel, setSelectdChannel] = useState();

    const onTransferMessage = () => { };

    const onApprove = () => {
        axios
            .post(API_TICKET.approvePriceTicket, {
                channelId: selectedChannel?._id
            })
            .then(({ status, data }) => {
                if (status === 200) {
                    toast.success(data?.message);
                }
            });
    };

    const openChat = (_channel) => {
        axios
            .post(API_CHAT.oneChannelData, {
                channelId: _channel?._id,
                received: isReceived === 'received'
            })
            .then((res) => {
                if (res.status === 200) {
                    if (res.data?.data?.length > 0) setSelectdChannel(res.data.data[0]);
                }
            });
    };
    useEffect(() => {
        if (ticketId) {
            axios
                .post(API_CHAT.channelData, {
                    ticketId,
                    received: isReceived === 'received'
                })
                .then((res) => {
                    if (res.status === 200) {
                        const _channels = res.data.data;
                        setChannels(_channels);
                        setChannel(_channels[0]);
                    }
                });
        }
        // eslint-disable-next-line
    }, [ticketId]);

    const onShowUserList = () => {
        const modal = document.querySelector('#user-list-modal');
        if (modal) {
            modal.click();
        }
    };
    return (
        <Page title="Conversation Yard">
            <PageBanner>
                <div className="container p-4">
                    <div className="flex flex-col gap-4 mb-8">
                        <h3 className="text-4xl text-primary">{t('title.select_ticket')}</h3>
                    </div>
                </div>
            </PageBanner>
            <div className="container p-4 mb-8">
                <GradientBorderWrapper>
                    <div className="w-full p-2 gap-2 flex">
                        <div className="p-2 md:hidden">
                            <button className="btn btn-secondary btn-circle btn-sm" onClick={onShowUserList}>
                                <Icon icon="ic:round-menu" />
                            </button>
                        </div>
                        <div className="w-full p-2 gap-2 flex-1 flex flex-col md:flex-row md:items-center">
                            <h4 className="text-primary font-2xl">{channel?.eventName}</h4>
                            <h6>
                                {channel?.eventType} - {channel?.count} {t('title.tickets')}
                            </h6>
                            <h6 className="text-sm text-stone-400">{fDescriptionTypeDate(channel?.eventDate)}</h6>
                        </div>
                        {selectedChannel && !selectedChannel?.dispute && selectedChannel?.receiver === user?._id && (
                            <button className="btn btn-primary btn-sm capitalize" onClick={onApprove}>
                                {t("action.approve")} R$ {selectedChannel.buyerPrice}
                            </button>
                        )}
                    </div>
                    <div className="w-full p-2 flex gap-1">
                        <div className="w-[250px] hidden md:block">
                            <UserList channels={channels} openChat={openChat} isReceiver={isReceived === 'received'} />
                        </div>
                        <ChatPanel
                            onTransferMessage={onTransferMessage}
                            selectedChannel={selectedChannel}
                            isReceiver={isReceived === 'received'}
                        />
                    </div>
                </GradientBorderWrapper>
            </div>
            <UserListModal
                channels={channels}
                openChat={openChat}
                isReceiver={isReceived === 'received'}
                onClose={onShowUserList}
            />
        </Page>
    );
}
