import { useCallback, useRef, useEffect, useState } from 'react';
import { Icon } from '@iconify/react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import SimpleBar from 'simplebar-react';

import { API_CHAT, HOST_API, WEBSOCKET_CONSTATNT } from '../../config';

import useAuth from '../../hooks/useAuth';
import useWebsocket from '../../hooks/useWebsocket';

import axios from '../../utils/axios';
import { fToNow } from '../../utils/formatTime';
import { fNumber } from '../../utils/formatNumber';

import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function ChatPanel({ onTransferMessage, selectedChannel, isReceiver = false }) {
    const [rows, setRows] = useState(1);
    const [message, setMessage] = useState('');
    const scrollRef = useRef(null);
    const [chats, setChats] = useState(selectedChannel?.chats);
    const { user } = useAuth();
    const { received, send } = useWebsocket();
    const handleChange = (e) => {
        const _message = e.target.value;
        setRows(_message?.split('\n').length);
        setMessage(_message);
    };

    const onMsgFocus = () => {
        const unReadedReceiveMsg = chats.filter((chat) => chat.receiver === user?._id && chat.readed === false);
        if (unReadedReceiveMsg.length)
            axios.post(API_CHAT.readedMessage, { channelId: selectedChannel?._id, receiver: isReceiver ? selectedChannel?.sender : selectedChannel?.receiver }).then((res) => {
                if (res.status === 200) {
                    const readedChats = chats?.map((chat) => {
                        if (chat.receiver === user?._id)
                            chat.readed = true;
                        return chat;
                    });
                    setChats(readedChats);
                }
            });
    }

    const handleTransferMessage = useCallback(() => {
        const data = {
            channelId: selectedChannel?._id,
            receiver: isReceiver ? selectedChannel?.sender : selectedChannel?.receiver,
            content: message
        };
        axios.post(API_CHAT.saveMessage, data).then((res) => {
            if (res.status === 200) {
                onTransferMessage(message);
                setChats([...chats, res.data.data]);
                send({
                    command: WEBSOCKET_CONSTATNT.USER_CHAT,
                    content: data
                });
                setMessage('');
                setRows(1);
            }
        });
        // eslint-disable-next-line
    }, [selectedChannel, message]);

    useEffect(() => {
        if (selectedChannel) {
            setChats(selectedChannel?.chats);
        }
    }, [selectedChannel]);

    useEffect(() => {
        if (received && received?.command) {
            const { command, content, sender } = received;
            if (command === WEBSOCKET_CONSTATNT.USER_CHAT && content.receiver && content?.receiver === user?._id) {
                const _message = content?.content;
                const _channelId = content?.channelId;
                // update selected channel
                if (_channelId === selectedChannel?._id) {
                    const chat = {
                        created: new Date(Date.now()),
                        content: _message,
                        receiver: user?._id,
                        readed: false
                    };
                    setChats([...chats, chat]);
                }
                // update user list
                else {
                    const countBadge = document.querySelector(`#count-${sender}`);

                    if (countBadge) {
                        const value = countBadge?.textContent || 0;
                        countBadge.classList.remove('hidden');
                        countBadge.textContent = fNumber(parseInt(value) + 1);
                    }
                }
            } else if (command === WEBSOCKET_CONSTATNT.READ_MESSAGE) {
                const readedChats = chats?.map((chat) => {
                    if (chat.sender === user?._id)
                        chat.readed = true;
                    return chat;
                });
                setChats(readedChats);
            }
        }
        // eslint-disable-next-line
    }, [received]);

    useEffect(() => {
        if (scrollRef.current && chats?.length && chats?.length > 8) {
            scrollRef.current?.scrollIntoView({ behavior: 'smooth', block: 'center', inline: "nearest" })
        }
    }, [chats]);

    return (
        <div className="flex-1">
            <GradientBorderWrapper>
                <div className="flex flex-col gap-1 p-1">
                    <SimpleBar id="simpleBar" className="max-h-[80vh] min-h-[80vh]">
                        <div className="w-full p-2">
                            {chats?.map((chat, index) => {
                                const myself = user?._id === chat?.sender;
                                const avatar = `${HOST_API}${myself ? user?.avatar : selectedChannel?.user?.avatar}`;
                                return (
                                    <div className={`chat ${myself ? 'chat-end' : 'chat-start'}`} key={index}>
                                        <div className="chat-image avatar">
                                            <div className="w-10 rounded-full">
                                                <LazyLoadImage src={avatar} alt="" />
                                            </div>
                                        </div>
                                        <div className="chat-header">
                                            <time className="text-xs opacity-50">{fToNow(chat?.created)}</time>
                                        </div>
                                        <p className="chat-bubble">
                                            {chat?.content}
                                            <span className={`text-xs flex ${myself ? 'justify-end' : 'justify-start'}`}>
                                                <Icon icon={chat?.readed ? "mdi:check-all" : "cil:check-alt"} />
                                            </span>
                                        </p>
                                    </div>
                                );
                            })}
                            <div ref={scrollRef} className="w-full h-4" />
                        </div>
                    </SimpleBar>
                    <div className="flex gap-1 flex-col md:flex-row">
                        <textarea
                            className="flex-1 textarea textarea-bordered"
                            rows={rows}
                            onFocus={onMsgFocus}
                            onChange={handleChange}
                            value={message}
                        />
                        <button
                            className="btn btn-primary"
                            onClick={handleTransferMessage}
                            disabled={!selectedChannel || selectedChannel === null}
                        >
                            SD
                        </button>
                    </div>
                </div>
            </GradientBorderWrapper>
        </div>
    );
}
