import { useEffect, useState } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';

import { HOST_API, WEBSOCKET_CONSTATNT } from '../../config';

import { fNumber } from '../../utils/formatNumber';

import useAuth from '../../hooks/useAuth';
import useWebsocket from '../../hooks/useWebsocket';

import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function UserList({ channels, isReceiver, openChat }) {
    const [currentChannel, setCurrnetChannel] = useState();
    const { user } = useAuth();
    const [onlineUsers, setOnlineUsers] = useState([]);
    const { received } = useWebsocket();

    const handleOpenChat = (channel) => {
        setCurrnetChannel(channel);
        openChat(channel);
    };

    function getUnreadCount(chats) {
        return chats.filter((c) => !c.readed && c.sender !== user?._id).length;
    }
    useEffect(() => {
        if (received && received.command) {
            const { command } = received;
            if (command === WEBSOCKET_CONSTATNT.ONLINE_USERS) {
                setOnlineUsers(received?.users);
            }
        }
    }, [received]);
    return (
        <GradientBorderWrapper>
            <div className="w-full p-2 flex flex-col gap-1">
                {channels?.map((channel, index) => (
                    <div
                        className="flex flex-col w-full cursor-pointer"
                        key={index}
                        onClick={() => handleOpenChat(channel)}
                    >
                        <div
                            className={`flex justify-between p-2 rounded-md ${
                                channel === currentChannel ? 'bg-secondary/50' : ''
                            }`}
                        >
                            <div className="flex gap-1">
                                <div className="avatar h-12 w-12 flex-1 aspect-square">
                                    <LazyLoadImage
                                        src={`${HOST_API}${channel?.user?.avatar}`}
                                        alt=""
                                        effect="blur"
                                        className="aspect-square rounded-full"
                                    />
                                </div>

                                <div className="flex flex-col justify-between">
                                    <span className="text-lg capitalize">{channel?.user?.fullName}</span>
                                    <span className="text-success" id={`online-${channel?.user?._id}`}>
                                        {onlineUsers?.includes(channel?.user?._id) ? 'online' : ' '}
                                    </span>
                                </div>
                            </div>
                            <div className="flex flex-col gap-1 justify-between">
                                {isReceiver && <span className="text-primary">R$ {fNumber(channel?.buyerPrice)}</span>}

                                <span
                                    className={`badge badge-primary ${
                                        getUnreadCount(channel?.chats) <= 0 ? 'hidden' : ''
                                    }`}
                                    id={`count-${channel?.user?._id}`}
                                >
                                    {fNumber(getUnreadCount(channel?.chats))}
                                </span>
                            </div>
                        </div>
                        <div className="divider -my-2  mx-1" />
                    </div>
                ))}
            </div>
        </GradientBorderWrapper>
    );
}
